//css variables

//Color codes
$body-bg: #f2f6ff;
$white: #fff;
$black: #000;

$primary_color: #2150d6;
$primary_color_ultra_light: #2151d633;
$primary_text: #5f6a85;
$Ninja: ".Ninja";
$Ninjai: ".Ninjai";

$font-title: "4em";

@font-face {
  font-family: "bokor";
  src: url("/font/bokor.ttf");
}
* {
  scroll-margin-top: 120px;
}
body {
  padding: 0;
  height: 100%;
  font-family: "bokor";
  background-image: linear-gradient(to bottom right, #ececec, #ded0d0);
  background-repeat: no-repeat;
  color: #5e0000;
}
p {
  margin: 0;
}
main {
  flex: 4;
  overflow: scroll;
}

#{$Ninja} {
  &-header {
    background-color: #efefef;
    color: #5e0000;
    display: flex;
    box-shadow: 0 0 4px #720000;
    margin: 20px;
    border-radius: 5px;
    align-items: center;
    padding: 12px 10px;
    box-sizing: border-box;
    gap: 2em;
    position: sticky;
    top: 20px;
    &-nav {
      flex: 1;
      display: flex;
      gap: 4em;
      justify-content: center;
      overflow: hidden;
      align-items: center;
      &__item {
        color: #5e0000;
        font-size: 1.2em;
        text-decoration: none;
        &:hover {
          text-shadow: 0 0 1px#720000;
          cursor: pointer;
        }
      }
    }
    &-hamburger {
      display: none;
      border-radius: 6px;
      align-items: center;
      &-list {
        background-color: #efefef;
        flex-direction: column;
        box-shadow: 0 0 4px #720000;
        position: absolute;
        margin: auto;
        top: 90px;
        width: 100%;
        left: 0;
        border-radius: 4px;
        overflow: hidden;
        display: none;
        &-item {
          color: #720000;
          font-size: 1.2em;
          text-decoration: none;
          display: flex;
          align-items: center;
          justify-content: center;
          padding: 5px 0;
          &:hover {
            background-color: #c3b5b5;
          }
        }
        &-show {
          display: flex;
        }
      }
    }
    &-socialmedia {
      display: flex;
      gap: 20px;
      margin-left: auto;
      margin-right: 10px;
      &-item {
        padding: 4px;
        border-radius: 4px;
        cursor: pointer;
        transition: ease 0.3s background-color;
        &:hover {
          background-color: #d3b1b1;
        }
      }
    }
  }
  &-home {
    display: flex;
    align-items: center;
    margin-top: 5em;
    padding: 0 8em;
    flex-wrap: wrap;
    gap: 5em;
    &-image {
      flex: 1;
      width: 100%;
      height: auto;
    }
    &-content {
      display: flex;
      flex-direction: column;
      flex: 1;
      align-items: center;
      &__title {
        margin-bottom: 60px;
        width: 100%;
        height: auto;
      }
      &__text {
        font-size: 24px;
        text-align: center;
      }
    }
  }
  &-smash-it {
    display: flex;
    margin-top: 5em;
    padding: 0 8em;
    flex-wrap: wrap;
    gap: 5em;
    &-image {
      flex: 1;
      width: 100%;
      height: auto;
    }
    &-content {
      flex: 1;
      &__title {
        font-size: #{$font-title};
        margin: 0;
      }
      &__text {
        margin-top: 40px;
        font-size: 24px;
      }
    }
  }
  &-purchase {
    margin-top: 5em;
    padding: 0 8em;
    &__title {
      font-size: #{$font-title};
      margin: 0;
    }
    &-list {
      margin: 0;
      padding: 0;
      list-style-type: none;
      &-item {
        margin: 0;
        padding: 0;
        display: flex;
        font-size: 24px;
        margin-top: 20px;
        &-icon {
          margin-right: 30px;
        }
      }
    }
  }
  &-tokenomics {
    margin-top: 5em;
    background-image: linear-gradient(to bottom, #7b1212, #b01616);
    color: #fff;
    padding: 0 8em;
    display: flex;
    flex-direction: column;
    &__title {
      font-size: #{$font-title};
      margin: 0;
    }
    &-list {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-around;
      &-item {
        display: flex;
        flex-direction: column;
        padding-bottom: 50px;
        justify-content: space-around;
        align-items: center;
        &__value {
          font-size: 150px;
        }
        &__title {
          font-size: 30px;
          text-align: center;
          margin: 0;
        }
      }
    }
  }
  &-roadmap {
    margin-top: 5em;
    padding: 0 8em;
    display: flex;
    flex-direction: column;
    background-image: url("../images/road-map-bg.svg");
    background-repeat: no-repeat;
    background-size: 100%;
    &-image {
      width: 100%;
      height: auto;
    }
  }
}

@media only screen and (max-width: 768px) {
  #{$Ninja} {
    &-header {
      &-nav {
        display: none;
      }
      &-hamburger {
        display: inline-flex;
        margin-left: auto;
      }
    }
    &-home,
    &-smash-it,
    &-purchase,
    &-tokenomics,
    &-roadmap {
      padding: 0 3em;
    }
    &-tokenomics {
      &-list {
        &-item {
          width: 100%;
        }
      }
    }
    &-roadmap {
      align-items: center;
    }
  }
}
